body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
    margin: 0;
    padding: 0;
}

.main{
    width: 100%;
    height: 100vh;
}

video{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

.content h1{
    font-size: 5em;
}

.overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 109.5%;
    background-color: rgba(0, 0, 0, 0.3);
}


@media screen and (max-width: 582px){
    .content h1{
        font-size: 3em;
    }
}
.why {
    background-color: #f4f4f4;
    padding: 10px;
}

.why h1 {
    font-size: 3em;
    margin: 1em;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
}

.why_about {
    font-size: 20px;
    display: flex;
    align-items: center;
    width: 60%;
    padding: 5px;
}

.why_data {
    /* margin: 5PX; */
    margin-top: -20px;
    display: flex;
    justify-content: center;
    flex: 0 0 auto;
}

.why_data iframe {
    border-radius: 5px;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    width: 50%;
}

@media screen and (max-width: 1100px) {
    .why_data {
        flex-flow: row wrap;
    }

    .why_data iframe {
        display: block;
        order: 1;
        width: 70%;
        /* height: auto; */
    }

    .why_about {
        display: block;
        order: 2;
        width: 100%;
        /* padding: 20px 35px; */
    }
}

@media screen and (max-width: 760px) {
    .why h1 {
        font-size: 2em
    }

    .why_data {
        flex-flow: row wrap;
    }

    .why_data iframe {
        display: block;
        order: 1;
        width: 80%;
        /* height: auto; */
    }

    .why_about {
        display: block;
        order: 2;
        width: 100%;
        padding: 5px;
    }
}


@media screen and (max-width: 500px) {
    .why_about {
        font-size: 15px;
    }
}
.under-line{
    height: 4px;
    width: 160px;
    background-color: #423F3E;
    margin: 10px auto;
    border-radius: 15px;
}
.under-line-middle{
    height: 10px;
    width: 40px;
    background-color: #429445;
    margin: 10px auto;
    position: relative;
    bottom: 16.5px;
    border-radius: 10px;
    margin-bottom: 20px;
}
.card-wrapper {
    display: flex;
}

.event_card_wrap {
    margin-top: 80px;
}

.event_card_wrap>.row {
    text-align: -webkit-center;
}
.pledge{
    background: linear-gradient(45deg,#55D284,#F2CF07);
    height: auto;
    width: 100%;
}

.pledge .head{
    word-spacing: 2px;
    font-size: 4em;
}

.pledge .slogan{
    font-size: 3em;
    /* word-spacing: 8px; */
    /* letter-spacing: 3px; */
}


/* .pledge .number{
    font-size: 7em;
} */

.pledge .btn{
    width: 8em;
    height: 2em;
    border-radius: 40px;
    background-color: #feb141;
    color: white;
    font-weight: 600;
    font-size: 2em;
}


.pledge_data{
    display: flex;
    justify-content: center;
    flex: 0 0 auto;
}

.pledge_about{
    /* display: flex; */
    align-items: center;
    width: 60%;
    margin: 5px;
}

.pledge_img{
    border-radius: 5px;
    display: flex;
    margin: auto;
    margin-left: 3em;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 40em;
    cursor: pointer;
}

@media screen and (max-width: 1100px) {
    .pledge_data {
        flex-flow: row wrap;
    }

    .pledge_img {
        display: block;
        order: 1;
        width: 70%;
        /* height: auto; */
        margin: auto;
    }

    .pledge_about {
        display: block;
        order: 2;
        width: 100%;
        padding: 20px 35px;
    }
}



@media screen and (max-width: 760px) {
    /* .why h1 {
        font-size: 2em
    } */

    .pledge_data {
        flex-flow: row wrap;
    }

    .pledge_img {
        display: block;
        order: 1;
        width: 80%;
    }

    .pledge_about {
        display: block;
        order: 2;
        width: 100%;
        padding: 20px 35px;
    }

    .pledge .btn{
        width: auto;
        font-size: 1.5em;
    }
}


@media screen and (max-width: 500px) {
    .pledge .slogan{
        font-size: 2em;
    }
}
.blog_data img{
    border-radius: 50%;
    width: 10em;
    height: 10em;
    margin: 20px;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    margin-left: 1em;
    margin-right: 1em;
}

.blog_data{
    display: flex;
    align-items: center;
    /* width: 60%; */
    flex: 0 0 auto;
}

.blog_data .btn{
    width: 10em;
    height: auto;
    border-radius: 40px;
    background-color: #fed501;
    color: white;
    font-weight: 600;
    font-size: 1em;
}

.blog_about{
    /* display: flex; */
    align-items: center;
}



@media screen and (max-width: 820px){
    .blog_data{
        flex-flow: row wrap;
    }

    .blog_data img{
        display: block;
        /* width: 80%; */
        margin: auto;
        order: 1;
    }

    .blog_about {
        display: block;
        order: 2;
        width: 100%;
        margin-top: 1em;
    }

    .blog_data .btn{
        justify-content: center;
        margin: auto;
        display: flex;
    }
}


@media screen and (max-width: 1200px){
    .blog iframe{
        margin-top: 1em;
        /* width: 0%; */
    }
}

@media screen and (max-width: 560px){
    .blog iframe{
        height: 250px;
        width: 400px;
    }
}

@media screen and (max-width: 445px){
    .blog iframe{
        height: 200px;
        width: 300px;
    }
}

.blog iframe{
    border-radius: 10px;
}
.footer{
    background-color: #212121;
    color: #949494;
    height: auto;
    margin-bottom: -2em;
}

.footer span{
    color: #ffffff;
}
.loading {
  z-index: 1000;
  /* background-color: #fff; */
  /* position: fixed; */
  top: 0;
  /* left: 0; */
  /* right: 0; */
  width: 100vw;
  height: 100vh;
  display: flex;
  /* flex-direction: column; */
  /* justify-content: center; */
  background-color: white;
}

.loading .loader {
  /* margin: auto; */
  /* align-items: center; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.loader img {
  width: 300px;
  height: 200px;
  -webkit-animation: mnkImg 4s linear infinite;
          animation: mnkImg 4s linear infinite;
  position: relative;
}

@-webkit-keyframes mnkImg {
    0% {
      left: -100%;    }
    100% {
      left: 400%;
    }
    /* 100%{
      left: -10%;
    } */
  }

@keyframes mnkImg {
    0% {
      left: -100%;    }
    100% {
      left: 400%;
    }
    /* 100%{
      left: -10%;
    } */
  }

